.header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
  background-color:#12182980;
  backdrop-filter: blur(20px);
  z-index: 1;
}
.container {
  width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

